.navbar {
    background-color: #8918e5;
    color: rgb(34, 18, 18);
    padding: 10px 20px;
    display: flex;
    justify-content: space-between;
    align-items: center;
}
  
.logo {
    font-size: 1.5rem;
}
  
.actions button {
    background-color: #4f0ee6;
    color: white;
    border: none;
    padding: 8px 16px;
    margin-left: 10px;
    cursor: pointer;
}
  
.actions button:hover {
    background-color: #2012e0;
}
  
.actions div {
    display: flex;
    align-items: center;
}
  
.actions div button {
    margin-left: 10px;
}
